*{
    margin: 0px;
    padding: 0px;
    font-family: Helvetica, sans-serif;
    background-color: gainsboro;
}
.nav{
    height: 40px;
    width: cacl(100% - 10px);
    background-color:blue;
    display: flex;
    align-items: center;
    color: white;
    justify-content: space-between;
    padding: 10px;
}
.nav-icon{
    width: 19.2px;
    height: 19.2px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:blue;
    color: white;
}
h1{
    font-size: 15px;
    background-color:blue;
    color: white;
}
.menu-desplegable{
    width: 100%;
    height: 150px;
    background-color: blue;
    border-top: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.boton-menu{
    background-color: white;
    color: blue;
    border: none;
    margin: 5px;
    width: 90%;
}
.carrito{
    width: calc(100% - 10px);
    height: 50px;
    background-color: white;
    border-radius: 3px;
    margin: 5px;
}
.productos{
    width: 100%;
    height: 100%;
    background-color: gainsboro;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.producto{
    height: 230px;
    width: calc(100% - 10px);
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 3px;
    margin-top: 5px;
}
.foto{
    width: 150px;
    height: 150px;
    border-radius: 3px;
    margin: 5px;
}
.precio{
    width: 90px;
    height: 10px;
    font-size: 15px;
    margin: 10px;
    background-color: white;
    color: blue;
    display: flex;
    justify-content: center;
    align-items: center;
}
.boton{
    color: white;
    border: 0px;
    background-color: blue;
    width: 90px;
    height: 15px;
    margin: 5px;
    font-size: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.boton-container{
    width: calc(100% - 10px);
    background-color: white;
    margin: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
}